import React, { PureComponent } from 'react'
import { getDate } from '../../utils/date'
import filter from '../../utils/filter'
import Query from '../Query'

interface IFormProps {
  match?: any
  query: string
  defaults: any
  skipOnUpdate?: string[]
  clientOnly?: string[]
}

export interface IProps {
  match: any
}
class Form extends PureComponent<IFormProps> {
  mergeData(defaults: any, data: any) {
    const result: any = {}
    Object.keys(defaults).forEach(e => {
      result[e] = { ...defaults[e] }
      if (defaults[e].type === 'date') {
        result[e].default = getDate(data[e])
      } else if (defaults[e].default instanceof Array) {
        result[e].current =
          data[e] &&
          (data[e][result[e].mapping && result[e].mapping.value] || data[e])
      } else if (
        typeof defaults[e].default === 'string' ||
        typeof defaults[e].default === 'number'
      ) {
        result[e].default = data[e]
      } else if (defaults[e].type === Boolean) {
        result[e].default = data[e]
      } else if (defaults[e].type === 'custom') {
        // result[e].default = data[e]
      } else if (defaults[e].default instanceof Object) {
        result[e].default = { ...this.mergeData(defaults[e].default, data[e]) }
      } else {
        throw new Error('merge error')
      }
    })
    return result
  }
  render() {
    const { edit = false, ...restParams }: any = this.props.match.params
    const {
      query,
      getQueryVariables = ({ id }: { id: string }) => ({ id }),
      defaults: prevDefaults,
      skipOnUpdate,
      clientOnly,
    }: any = this.props
    let defaults = prevDefaults
    if (edit === 'edit') {
      defaults = filter(prevDefaults, skipOnUpdate)
      return (
        <Query
          query={query}
          variables={getQueryVariables(restParams)}
          fetchPolicy='cache-and-network'
        >
          {({ loading, error, data }: IGraphqlData) => {
            if (loading) {
              return <span>loading...</span>
            } else if (error) {
              return <span>Error Occured!</span>
            }
            let newDefaults = this.mergeData(
              filter(defaults, clientOnly),
              data[Object.keys(data)[0]],
            )
            newDefaults = Object.assign(defaults, newDefaults)
            return (
              this.props.children instanceof Function &&
              this.props.children({
                update: true,
                defaults: newDefaults,
                queryResult: data,
              })
            )
          }}
        </Query>
      )
    }
    // else {
    //   defaults = filter(defaults, clientOnly)
    // }
    return (
      this.props.children instanceof Function &&
      this.props.children({ update: false, defaults, queryResult: null })
    )
  }
}

export default Form
