import InputAdornment from '@material-ui/core/InputAdornment'
import gql from 'graphql-tag'
import { withFormState } from 'informed'
import React, { PureComponent } from 'react'
import {
  CREATE_SITE_ENTRY,
  UPDATE_SITE_ENTRY,
} from '../../graphql/mutation/remote'
import { SITE_ENTRY } from '../../graphql/query/remote'
import { getDate } from '../../utils/date'
import toCurrency from '../../utils/toCurrency'
import Query from '../Query'
import Form from './Form'
import FormGenerator from './FormGenerator'

const TotalCostComponent = withFormState(((props: any) => {
  const values = props.formState.values
  let totalCost = 0
  let managerTotalCost = 0
  Object.values(values).forEach((element: any) => {
    totalCost += Number(element.cost) || 0
    managerTotalCost += (element.paid && Number(element.cost)) || 0
  })
  return (
    <>
      <p style={{ color: 'green' }}>
        <label>Manager Total Cost:</label> {toCurrency(managerTotalCost)}
      </p>
      <p style={{}}>
        <label>Total Cost:</label> {toCurrency(totalCost)}
      </p>
    </>
  )
}) as any)

// const StockAvailComponent = withFormState(((props: any) => {
//     const values = props.formState.values;
//     let available = values[props.stock];
//     return <>
//         <p style={{ color: 'green' }}><label>Available: </label> {available}</p>
//     </>
// }) as any)
class CreateSiteEntry extends PureComponent<any> {
  render() {
    const { user } = this.props
    const { siteId, id } = this.props.match.params
    const repeater = {
      quantity: {
        type: 'number',
        default: 0,
        required: true,
      },
      cost: {
        type: 'number',
        default: 0,
        required: true,
        startAdornment: <InputAdornment position='start'>â‚¹</InputAdornment>,
        // validate: validate("mistri")
      },
      paid: {
        type: Boolean,
        default: false,
        required: true,
      },
    }
    const defaults: IFormGenerator = {
      mistri: {
        default: { ...repeater },
      },
      labour: {
        default: { ...repeater },
      },
      eit: {
        default: { ...repeater },
      },
      morang: {
        default: { ...repeater },
      },
      baalu: {
        default: { ...repeater },
      },
      githi: {
        default: { ...repeater },
      },
      cement: {
        default: { ...repeater },
      },
      saria: {
        default: { ...repeater },
      },
      dust: {
        default: { ...repeater },
      },
      other: {
        default: {
          quantity: {
            type: 'text',
            default: 'N/A',
            required: true,
          },
          cost: {
            type: 'number',
            default: 0,
            required: true,
          },
          paid: {
            type: Boolean,
            default: false,
            required: true,
          },
        },
      },
      other2: {
        label: 'other 2',
        default: {
          quantity: {
            type: 'text',
            default: 'N/A',
            required: true,
          },
          cost: {
            type: 'number',
            default: 0,
            required: true,
          },
          paid: {
            type: Boolean,
            default: false,
            required: true,
          },
        },
      },
      note: {
        type: 'text',
        default: '',
      },
      createdAt: user.isAdmin()
        ? {
            type: 'date',
            default: getDate(),
          }
        : null,
      totalCost: {
        type: 'custom',
        default: <TotalCostComponent />,
      },
    }
    return (
      <Query
        query={gql`
          query {
            stocks {
              totalCount
              edges {
                name
                available
              }
            }
          }
        `}
      >
        {({ data }: IGraphqlData) => {
          data.stocks.edges.forEach((stock: any) => {
            defaults[stock.name].default.quantity = {
              ...defaults[stock.name].default.quantity,
            }
            defaults[stock.name].default = {
              available: {
                type: 'custom',
                default: (
                  <span style={{ color: 'green' }}>
                    Stock Available: {toCurrency(stock.available)}
                  </span>
                ),
              },
              ...defaults[stock.name].default,
            }
            // defaults[stock.name].label = (defaults[stock.name].label || stock.name) + `(${stock.available})`
          })
          return (
            <Form
              {...this.props}
              query={SITE_ENTRY}
              defaults={defaults}
              clientOnly={['totalCost']}
            >
              {({ update, defaults: ldefaults }: IFormProps) => {
                return (
                  <FormGenerator
                    {...this.props}
                    defaults={ldefaults}
                    mutation={update ? UPDATE_SITE_ENTRY : CREATE_SITE_ENTRY}
                    title={!update ? 'Create a new entry' : 'Update this entry'}
                    submitText={!update ? 'Create entry' : 'Update entry'}
                    mutateFunc={(fn, formData) => {
                      return fn({ variables: { siteId, id, data: formData } })
                    }}
                  />
                )
              }}
            </Form>
          )
        }}
      </Query>
    )
  }
}

export default CreateSiteEntry
